import React, { useState, useEffect, useContext } from "react";
import Header from "../Header";
import "./SecondQuestion.css";
import DatePicker from "react-date-picker";
import SecondLogo from "../../assets/vaccinate2.png";
import SecondLine from "../../assets/secondline.svg";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import { useHistory } from "react-router-dom";
import { Context } from "../../Context";
function SecondQuestion() {
  const [covidPositive, setCovidPositive] = useState(false);
  const [showAntigen, setShowAntigen] = useState("");
  const [antigenValue, setAntigenValue] = useState("");
  const [disableButton, setDisableButton] = useState(true);
  const { secondPage } = useContext(Context);
  const [secondQuestion, setSecondQuestion] = secondPage;

  let history = useHistory();

  const handlePositive = (e) => {
    setCovidPositive(true);
    setSecondQuestion({
      ...secondQuestion,
      covid_status: e.target.value,
      positive_checked: true,
      negative_checked: false,
      now_checked: false,
    });
  };

  const handleNegative = (e) => {
    setCovidPositive(false);
    console.log(e.target.value);
    setSecondQuestion({
      ...secondQuestion,
      covid_status: e.target.value,
      negative_checked: true,
      now_checked: false,

      antigen_test: "",
      positive_checked: false,
      antigen_status: "",
      antigen_checked: false,
      antigen_date: "",
      negativeAntigen_checked: false,
      covid_date: "",
    });
  };
  const handleNow = (e) => {
    console.log(e.target.value);
    setCovidPositive(false);
    setSecondQuestion({
      ...secondQuestion,
      covid_status: e.target.value,
      now_checked: true,

      antigen_test: "",
      positive_checked: false,
      negative_checked: false,
      covid_date: "",
      antigen_status: "",
      antigen_checked: false,
      antigen_date: "",
      negativeAntigen_checked: false,
    });
  };

  const handleAntigen = (e) => {
    if (e.target.value === "·Éô·Éò") {
      setSecondQuestion({
        ...secondQuestion,
        negativeAntigen_checked: false,
        antigen_checked: true,
        antigen_status: e.target.value,
      });
    } else if (e.target.value === "·Éê·É†·Éê") {
      setAntigenValue("");
      setSecondQuestion({
        ...secondQuestion,
        antigen_checked: false,
        negativeAntigen_checked: true,
        antigen_status: e.target.value,
        antigen_value: "",
        antigen_date: "",
      });
    }
  };

  const hanldeChange = (e) => {
    setAntigenValue(e.target.value);

    setSecondQuestion({
      ...secondQuestion,
      antigen_value: e.target.value,
    });
  };

  const antigenDateChange = (e) => {
    // setDate(e);
    console.log(e);
    setSecondQuestion({
      ...secondQuestion,
      antigen_date: e,
    });
  };

  const covidDateChange = (e) => {
    setSecondQuestion({
      ...secondQuestion,
      covid_date: e,
    });
  };

  console.log(secondQuestion);
  useEffect(() => {
    if (
      secondQuestion.covid_status === "·Éô·Éò" &&
      secondQuestion.antigen_status === "·Éô·Éò" &&
      secondQuestion.antigen_date !== "" &&
      secondQuestion.antigen_date !== null &&
      secondQuestion.antigen_value.length > 1
    ) {
      console.log("egaa");
      setDisableButton(false);
    } else {
      setDisableButton(true);
    }

    if (
      secondQuestion.covid_status === "·Éô·Éò" &&
      secondQuestion.antigen_status === "·Éê·É†·Éê" &&
      secondQuestion.covid_date !== "" &&
      secondQuestion.covid_date !== null
    ) {
      setDisableButton(false);
    }

    if (
      secondQuestion.covid_status === "·Éê·É†·Éê" ||
      secondQuestion.covid_status === "·Éê·ÉÆ·Éö·Éê ·Éõ·Éê·É•·Éï·É°"
    ) {
      setDisableButton(false);
    }
  }, [secondQuestion]);

  return (
    <>
      <Header page={"2"} />
      <div className="content">
        <div className="main_content">
          <p className="question__title">·Éí·Éê·É•·Éï·É° ·Éí·Éê·Éì·Éê·É¢·Éê·Éú·Éò·Éö·Éò covid-19?*</p>
          <div className="qqq">
            <label className="container">
              <p>·Éô·Éò </p>
              <input
                checked={secondQuestion.positive_checked}
                type="radio"
                name="radio"
                value="·Éô·Éò"
                onChange={handlePositive}
              />
              <span className="checkmark"></span>
            </label>
            <label className="container">
              <p>·Éê·É†·Éê </p>
              <input
                value="·Éê·É†·Éê"
                checked={secondQuestion.negative_checked}
                type="radio"
                name="radio"
                onChange={handleNegative}
              />
              <span className="checkmark"></span>
            </label>
            <label className="container">
              <p> ·Éê·ÉÆ·Éö·Éê ·Éõ·Éê·É•·Éï·É° </p>
              <input
                value="·Éê·ÉÆ·Éö·Éê ·Éõ·Éê·É•·Éï·É°"
                checked={secondQuestion.now_checked}
                type="radio"
                name="radio"
                onChange={handleNow}
              />
              <span className="checkmark"></span>
            </label>
          </div>
          {secondQuestion.positive_checked && (
            <>
              <p className="question__title">
                ·Éê·Éú·É¢·Éò·É°·ÉÆ·Éî·É£·Éö·Éî·Éë·Éò·É° ·É¢·Éî·É°·É¢·Éò ·Éí·Éê·Éô·Éî·Éó·Éë·É£·Éö·Éò ·Éí·Éê·É•·Éï·É°?*
              </p>
              <label className="container">
                <p>·Éô·Éò </p>
                <input
                  checked={secondQuestion.antigen_checked}
                  type="radio"
                  name="radio1"
                  value="·Éô·Éò"
                  onChange={handleAntigen}
                />
                <span className="checkmark"></span>
              </label>
              <label className="container">
                <p>·Éê·É†·Éê </p>
                <input
                  type="radio"
                  name="radio1"
                  value="·Éê·É†·Éê"
                  checked={secondQuestion.negativeAntigen_checked}
                  onChange={handleAntigen}
                />
                <span className="checkmark"></span>
              </label>
            </>
          )}

          {secondQuestion.antigen_checked && (
            <>
              <p className="question__title">
                ·Éó·É£ ·Éí·Éê·ÉÆ·É°·Éù·Éï·É°, ·Éí·Éó·ÉÆ·Éù·Éï ·Éõ·Éò·É£·Éó·Éò·Éó·Éî ·É¢·Éî·É°·É¢·Éò·É° ·Éõ·Éò·Éê·ÉÆ·Éö·Éù·Éî·Éë·Éò·Éó·Éò <br /> ·É†·Éò·É™·ÉÆ·Éï·Éò ·Éì·Éê
                ·Éê·Éú·É¢·Éò·É°·ÉÆ·Éî·É£·Éö·Éî·Éë·Éò·É° ·É†·Éê·Éù·Éì·Éî·Éú·Éù·Éë·Éê*
              </p>
              <DatePicker
                onChange={(e) => antigenDateChange(e)}
                // value={
                //   secondQuestion.antigen_date.length > 1
                //     ? secondQuestion.antigen_date
                //     : date
                // }
                value={secondQuestion.antigen_date}
                e
                maxDate={new Date()}
              />
              <form>
                <input
                  value={
                    antigenValue.length > 1
                      ? antigenValue
                      : secondQuestion.antigen_value
                  }
                  type="number"
                  placeholder="·Éê·Éú·É¢·Éò·É°·ÉÆ·Éî·É£·Éö·Éî·Éë·Éò·É° ·É†·Éê·Éù·Éì·Éî·Éú·Éù·Éë·Éê"
                  className="antigen__input"
                  onChange={hanldeChange}
                ></input>
              </form>
            </>
          )}

          {secondQuestion.negativeAntigen_checked && (
            <>
              <p className="question__title">
                ·Éõ·Éò·É£·Éó·Éò·Éó·Éî ·Éõ·Éò·Éê·ÉÆ·Éö·Éù·Éî·Éë·Éò·Éó·Éò ·Éû·Éî·É†·Éò·Éù·Éì·Éò (·Éì·É¶·Éî/·Éó·Éï·Éî/·É¨·Éî·Éö·Éò) <br />
                ·É†·Éù·Éì·Éò·É° ·Éí·É•·Éù·Éú·Éì·Éê Covid-19*
              </p>
              <DatePicker
                onChange={(e) => covidDateChange(e)}
                value={secondQuestion.covid_date}
                maxDate={new Date()}
              />
            </>
          )}
        </div>
        <img className="second__logo" src={SecondLogo} />
        <img className="second__line" src={SecondLine} />
      </div>
      <button className="chevron__button second__chevron__button">
        <ChevronLeftIcon onClick={() => history.push("/firstquestion")} />
      </button>

      <button
        disabled={disableButton}
        className="chevron__button second__chevron__button"
        // disabled={true}
        onClick={() => history.push("/thirdQuestion")}
      >
        <ChevronRightIcon />
      </button>
    </>
  );
}

export default SecondQuestion;
